
.top-nav {
  position: relative;
}
.logo-block {
  position: absolute;
  top: 0px;
}
.logo-block a {
  display: block;
  border: 12px solid #fff;
  border-radius: 20px;
}
.right-nav ul li {
  list-style-type: none;
}
.right-nav ul li h5,
.right-nav ul a {
  font-size: 14px;
  font-weight: 500;
}
#footer-wrap {
  background: #0d2249;
}
#footer-wrap h5 {
  padding: 15px;
  font-size: 12px;
  color: #ffffff;
  font-weight: 500;
  text-align: center;
}
.circle-input {
  background: #ffffff !important;
  border: 1px solid #efefef !important;
  border-radius: 50px;
  -webkit-border-radius: 50px !important;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  min-height: 60px !important;
  padding: 15px 20px !important;
  font-size: 14px !important;
  line-height: 17px !important;
  color: #5c5c5c;
}
.circle-input::-webkit-input-placeholder {
  color: #5c5c5c;
}
.circle-input::-moz-placeholder {
  color: #5c5c5c;
}
.circle-input:-ms-input-placeholder {
  color: #5c5c5c;
}
.circle-input:-moz-placeholder {
  color: #5c5c5c;
}

.body-wrap {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.middle-body {
  flex: 1;
  background: #0c2657;
  display: flex;
  align-items: center;
  justify-content: center;
}
#login-wrap {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}
.login-box {
  max-width: 430px;
  padding: 14px;
  margin: 0 auto;
}
.login-btn {
  background: rgb(255, 138, 0);
  background: -moz-linear-gradient(left, rgb(255, 138, 0) 0%, rgb(255, 192, 0) 100%);
  background: -webkit-linear-gradient(left, rgb(255, 138, 0) 0%, rgb(255, 192, 0) 100%);
  background: linear-gradient(to right, rgb(255, 138, 0) 0%, rgb(255, 192, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff8a00', endColorstr='#ffc000',GradientType=1 );
  min-height: 60px;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  border: 0px;
  width: 100%;
  font-size: 14px;
  text-align: center;
  color: #ffffff;
}
.login-title {
  font-size: 14px;
  color: #d8d8d8;
  font-weight: normal;
}
.signup-link {
  font-size: 12px;
  color: #d8d8d8;
  font-weight: normal;
}
.signup-link a {
  color: #ffc000;
}
@media (max-width: 767px) {
  /* For Login Media */
  .logo-block a {
    width: 48px;
    border: 4px solid #fff;
    border-radius: 12px;
  }
  .top-nav {
    padding: 6px 0px;
  }
  .right-nav ul li h5 {
    margin-right: 10px;
    font-size: 12px;
  }
  .right-nav ul li a {
    font-size: 12px;
  }
  .right-nav ul li span {
    margin: 0px 8px;
  }
  .login-box {
    padding: 0px;
  }
  .f-logo {
    width: 140px;
  }
  .login-box form {
    margin-top: 20px;
  }
  .circle-input,
  .login-btn {
    min-height: 40px;
    padding: 5px 10px;
  }
  .login-btn.mr-t40 {
    margin-top: 20px;
  }
}
