
/* Grade Page css */
.large-cir {
    height: 460px;
    width: 460px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    background-color: #c4c4c4;
    border: 8px solid #ffffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    -ms-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  .large-cir h4 {
    color: #1b1f5f;
    font-size: 50px;
    font-weight: 500;
    text-align: center;
  }
  .large-cir h4 span {
    font-weight: normal;
    display: block;
    font-size: 30px;
  }
.class-btn-block {
    position: absolute;
    top: 0;
    cursor: pointer;
    transition: all 0.5s linear 0s;
  }
  .class-btn-block span {
    width: 98px;
    height: 98px;
    background: #ffffff;
    border: 2px solid #979797;
    border-radius: 50px;
    color: #5c5c5c;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    font-weight: 500;
    transition: all 0.5s linear 0s;
}
.class-btn-block input {
    display: none;
}
  .class5 {
    transform: translate(0px, -40px);
  }
  .class6 {
    transform: translate(171px, 35px);
  }
  .class7 {
    transform: translate(217px, 210px);
  }
  .class8 {
    transform: translate(110px, 365px);
  }
  .class9 {
    transform: translate(-107px, 373px);
  }
  .class10 {
    transform: translate(-224px, 217px);
  }
  .class11 {
    transform: translate(-176px, 38px);
  }
  .class5:hover {
    transform: translate(0px, -40px) scale(1.3);
  }
  .class6:hover {
    transform: translate(171px, 35px) scale(1.3);
  }
  .class7:hover {
    transform: translate(217px, 210px) scale(1.3);
  }
  .class8:hover {
    transform: translate(110px, 365px) scale(1.3);
  }
  .class9:hover {
    transform: translate(-107px, 373px) scale(1.3);
  }
  .class10:hover {
    transform: translate(-224px, 217px) scale(1.3);
  }
  .class11:hover {
    transform: translate(-176px, 38px) scale(1.3);
  }
  .class-btn-block span:hover {
    background: #ff6f1e;
    border: 2px solid #ffffff;
    color: #ffffff;
    transition: all 0.5s linear 0s;
  }
  .class-btn-block input:checked ~ span {
    background: #ff6f1e;
    border: 2px solid #ffffff;
    color: #ffffff;
  }
  .orenge-btn {
    height: 64px;
    min-width: 180px;
    background: #ff6f1e;
    border-radius: 50px;
    font-size: 24px;
    color: #ffffff;
    border: 0px;
    padding: 10px;
    font-weight: 600;
  }