.top-header{
  z-index: 99;
  position: relative;
}

.banner-wrap {
    background: linear-gradient(208.6deg, #ffc000 1.55%, #ff8a00 95.8%);
    padding: 45px 0px 35px;
    position: relative;
  }
.banner-wrap::after{
    content: "";
    background: url("../../assets/images/banner-img.png") no-repeat;
    position: absolute;
    height: 100%;
    width: 332px;
    top: 0px;
    right: 10%;
    background-size: contain;
  }
  .banner-block p {
    font-size: 18px;
    color: #fff7df;
  }
  .banner-block h4 {
    color: #fff7df;
    font-size: 44px;
    font-weight: 500;
  }
  .s-form-wrap {
    background: #0c2657;
    padding: 50px 0px 80px;
  }
  
  .s-form-wrap .login-title {
    font-size: 24px;
  }
  .learning-step-wrap {
    padding: 50px 0px;
  }
  .learning-card {
    text-align: center;
  }
  .learning-card h4 {
    font-weight: 600;
    font-size: 20px;
    color: #5c5c5c;
    margin: 20px 0px 10px;
  }
  .learning-card p {
    font-size: 14px;
    color: #5c5c5c;
    line-height: 14px;
  }
  .section-divider {
    width: 160px;
    height: 7px;
    border-radius: 50px;
    background-color: #0054a6;
    position: relative;
  }
  .section-divider::before {
    content: "";
    width: 50%;
    height: 100%;
    border-radius: 50px;
    background-color: #ffc000;
    position: absolute;
    left: 0;
    top: 0;
  }
  .feedback-block {
    background: #ffffff;
    border: 1px solid #efefef;
    box-shadow: 0px 12px 35px rgba(160, 121, 0, 0.2);
    border-radius: 5px;
    padding: 30px;
    opacity: 0.4;
    transition: all 0.3s linear 0s;
  }
  .feedback-block:hover{
    box-shadow: 0px 12px 35px rgba(160, 121, 0, 0.2);
    opacity: 1;
    transition: all 0.3s linear 0s;
    transform: scale(1.1);
  }
  .feedback-block p {
    font-size: 14px;
    line-height: 22px;
    margin: 20px 0px 30px;
  }
  .feedback-slides .swiper-wrapper{
    padding: 60px 0px 30px;
  }
  .section-title {
    font-size: 40px;
    background: -webkit-linear-gradient(195.62deg, #ffc000 1.55%, #ff8a00 95.8%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .slider-wrap .swiper-container {
    width: 90%;
    position: absolute;
    top: 50%;
    background-color: #ffffff;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0px 12px 35px rgba(160, 121, 0, 0.2);
  }
  .feedback-slides .swiper-wrapper{
    padding: 60px 0px 30px;
  }
  .user-pro {
    display: flex;
    align-items: center;
  }
  .profile-img {
    width: 58px;
    height: 58px;
    background: #d8d8d8;
    border-radius: 50%;
    margin-right: 10px;
  }
  .profile-info h5 {
    font-weight: 600;
    font-size: 16px;
    color: #5c5c5c;
  }
  .profile-info p {
    margin: 0px;
    font-size: 12px;
    color: #5c5c5c;
  }
  .feedback-slides .swiper-wrapper{
    padding: 60px 0px 30px;
  }
  .count-status-wrap{
    padding: 50px 0px 50px;
    background-color: #FF9201;
  }
  .count-block{
    text-align: center;
  }
  .count-block h3{
    font-size: 40px;
    color: #FFFFFF;
    margin: 20px 0px;
  }
  .count-block p{
    font-size: 14px;
    color: #FFFFFF;
  }
  